ACE,gBAAgB;MAChB,yBAAyB;MACzB,gBAAgB;MAChB,iCAAiC;;MAEjC;QACE,sBAAsB;MACxB;;MAEA;QACE,6BAA6B;MAC/B;;MAEA;QACE,sBAAsB;MACxB;IACF;;IAEA;MACE,eAAe;MACf,WAAW;IACb;EACF;AACF;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,iBAAiB;EACjB,sBAAsB;;EAEtB;IACE,YAAY;IACZ,qBAAqB;IACrB,sBAAsB;EACxB;;EAEA;IACE,YAAY;EACd;;EAEA;IACE,kBAAkB;IAClB,QAAQ;IACR,aAAa;IACb,iBAAiB;IACjB,kCAAkC;;IAElC;MACE,aAAa;MACb,WAAW;IACb;EACF;AACF;;AAEA;EACE;IACE;MACE,0BAA0B;MAC1B,+BAA+B;;MAE/B;QACE,kBAAkB;QAClB,8BAA8B;MAChC;;MAEA;QACE,4CAA4C;MAC9C;;MAEA;;QAEE;UACE,mCAAmC;QACrC;;QAEA;UACE,WAAW;QACb;MACF;;MAEA;QACE,iBAAiB;MACnB;IACF;EACF;AACF;;AAEA;EACE;IACE,6BAA6B;EAC/B;;EAEA;IACE,2DAA2D;IAC3D,qEAAqE;;IAErE;MACE,wBAAwB;MACxB,aAAa;MACb,gCAAgC;MAChC,mBAAmB;MACnB,qBAAqB;;MAErB,qCAAqC;;MAErC,kBAAkB;MAClB,0BAA0B;;MAE1B;QACE,kBAAkB;MACpB;;MAEA;QACE,aAAa;QACb,4BAA4B;QAC5B,mBAAmB;QACnB,qBAAqB;MACvB;;MAEA;QACE,WAAW;MACb;IACF;;IAEA;MACE,wBAAwB;IAC1B;;IAEA;MACE,wBAAwB;IAC1B;EACF;AACF;;AAEA;EACE;IACE,8BAA8B;IAC9B,qBAAqB;IACrB,qBAAqB;EACvB;;EAEA;IACE,aAAa;IACb,iBAAiB;IACjB,sBAAsB;IACtB,YAAY;EACd;;EAEA;IACE,YAAY;;IAEZ,aAAa;IACb,sBAAsB;IACtB,gBAAgB;;IAEhB;MACE,gBAAgB;MAChB,eAAe;MACf,uBAAuB;IACzB;;IAEA;MACE,YAAY;MACZ,QAAQ;IACV;;IAEA;MACE,iBAAiB;MACjB,YAAY;MACZ;QACE,WAAW;;QAEX;UACE,wDAAwD;;UAExD;YACE,kBAAkB;UACpB;QACF;;QAEA;UACE,0BAA0B;UAC1B,aAAa;;UAEb;YACE,kBAAkB;UACpB;QACF;MACF;IACF;;IAEA;MACE,2BAA2B;MAC3B,mBAAmB;MACnB,iBAAiB;MACjB,qBAAqB;MACrB,YAAY;;MAEZ;QACE,WAAW;MACb;IACF;;IAEA;MACE,QAAQ;MACR,uBAAuB;MACvB,YAAY;;MAEZ;QACE,WAAW;MACb;IACF;;IAEA;MACE,iBAAiB;MACjB,QAAQ;MACR,iBAAiB;MACjB,2BAA2B;;MAE3B;QACE,eAAe;MACjB;;MAEA;QACE,gBAAgB;QAChB,gBAAgB;MAClB;;MAEA;QACE,eAAe;QACf,0BAA0B;QAC1B,uBAAuB;QACvB,oBAAoB;QACpB,cAAc;QACd,WAAW;QACX,YAAY;MACd;IACF;EACF;;EAEA;IACE,sBAAsB;IACtB,iBAAiB;IACjB,SAAS;IACT,gBAAgB;EAClB;AACF","sourcesContent":["*, *::after, *::before {\n  padding: 0px;\n  margin: 0px;\n  line-height: 1.5;\n}\n\n:root * {\n  font-family: inherit;\n  font-weight: inherit;\n}\n\n:root {\n  font-family: \"ZT-Nature\", Arial, system-ui, sans-serif;\n  font-weight: 600;\n\n  --project-container-width: max(350px, 25vw);\n  --create-container-width: max(200px, 12vw);\n  --header-height: 5rem;\n  --footer-height: 3rem;\n}\n\nbody {\n  display: grid;\n  height: 100vh;\n  grid-template-columns: 1fr;\n  grid-template-rows: var(--header-height) 1fr var(--footer-height);\n}\n\nbutton {\n  cursor: pointer;\n\n  &.save, &.exit, &.expand-btn, &.add-check-btn {\n    border: 2px solid black;\n    border-radius: 2px;\n    box-shadow: 1px 1px 1px lightgrey;\n    padding: 4px 8px;\n    transition: background-color 0.3s, transform 0.3s ease-out, box-shadow 0.3s;\n\n    &:hover {\n      transform: translate(-2px, -1px);\n      box-shadow: 3px 2px 1px lightgrey;\n    }\n  }\n}\n\n.header {\n  display: flex;\n  justify-content: center;\n  align-items: center;\n  padding: 10px;\n  font-weight: 700;\n  font-size: 1.4rem;\n\n  background-image: linear-gradient(90deg, orange, 50%, red);\n}\n\ndiv#content {\n  display: grid;\n  grid-template: 1fr / var(--create-container-width) 1fr var(--project-container-width);\n  max-height: calc(100vh - var(--footer-height) - var(--header-height));\n\n  .cards-container {\n    display: grid;\n    justify-content: center;\n    grid-template-columns: repeat(auto-fit, 300px);\n    grid-auto-rows: 160px;\n    gap: 40px 20px;\n    overflow: auto;\n    scrollbar-color: lightgreen transparent;\n\n    background-color: #ffffd0;\n    padding: 20px;\n\n    .card {\n      display: grid;\n      grid-template: 3fr 3fr 1fr 1fr / 16fr 7fr 1fr;\n      gap: 10px;\n      align-items: center;\n\n      box-sizing: border-box;\n      background-color: white;\n      border: 2px solid black;\n      padding: 10px;\n      box-shadow: 1px 1px 2px grey;\n\n      .title {\n        grid-column: 1 / 3;\n        font-weight: 700;\n        font-size: 1.5rem;\n      }\n\n      .description {\n        grid-column: 1 / 4;\n      }\n\n      .description, .title {\n        overflow: hidden;\n        white-space: nowrap;\n        text-overflow: ellipsis;\n      }\n\n      .expand-btn {\n        grid-row-end: span 2;\n        grid-column-end: span 2;\n        place-self: center;\n\n        background-color: #ffa500;\n        &:hover { background-color: #ffb500; }\n        &:active { background-color: #ffc500; }\n      }\n\n      .due-date, .priority {\n        white-space: pre-wrap;\n        font-size: 0.8rem;\n      }\n\n      .cross {\n        place-self: center;\n\n        svg {\n          cursor: pointer;\n          stroke: red;\n          transition: stroke-width 0.2s;\n\n          &:hover {\n            stroke-width: 3;\n          }\n        }\n      }\n\n      .due-date::before {\n        content: \"Due: \";\n      }\n\n      .priority::before {\n        content: \"Priority: \";\n      }\n    }\n  }\n\n  .projects-container {\n    display: flex;\n    width: var(--project-container-width);\n    flex-direction: column;\n    gap: 2px;\n    align-items: center;\n    padding: 10px 0px;\n    background-color: #8f008f;\n    overflow: auto;\n    scrollbar-color: lightgreen transparent;\n\n    & > * {\n      flex: 0 0 auto;\n    }\n\n    h2 {\n      color: white;\n      margin-bottom: 10px;\n    }\n\n    .drop-down-arrow {\n      display: none;\n    }\n\n    .project {\n      display: grid;\n      place-items: center stretch;\n      grid-template-columns: 9fr 1fr;\n      column-gap: 8px;\n\n      appearance: none;\n      cursor: pointer;\n      background-color: #faebd7;\n      width: 90%;\n      aspect-ratio: 4;\n      padding: 5px 10px 5px 20px;\n      font-size: 2rem;\n      border: 2px solid black;\n\n      span {\n        word-wrap: break-word;\n        text-align: left;\n        overflow: hidden;\n        font-size: 1rem;\n      }\n\n      box-shadow: 1px 1px 1px black;\n      transition: background-color 0.3s, transform 0.3s ease-out, box-shadow 0.3s;\n\n      &:hover {\n        background-color: #fafbe7;\n        transform: translate(-1px, -0.5px);\n        box-shadow: 2px 1.5px 1px black;\n      }\n\n      &:active { background-color: #fafbf7; }\n\n      &.selected {\n        background-color: #fad0c0;\n        cursor: default;\n\n        &:hover {\n          background-color: #fad0c0;\n          transform: translate(0px, 0px);\n          box-shadow: 1px 1px 1px black;\n        }\n      }\n    }\n\n    .remove-project-btn {\n      background-color: transparent;\n      border: none;\n\n      & > svg {\n        height: 2rem;\n        width: auto;\n        stroke: red;\n        transition: stroke-width 0.2s;\n\n        &:hover { stroke-width: 2.5; }\n      }\n    }\n  }\n\n  .create-container {\n    display: flex;\n    flex-direction: column;\n    justify-content: space-evenly;\n    align-items: center;\n\n    background-color: lightgreen;\n    padding: 0px 10px;\n\n    .add-item, .add-project {\n      display: grid;\n      grid-template: 1fr 4fr 1fr / 4fr 1fr;\n      place-items: center;\n    }\n\n    .add-project-input-container {\n      display: grid;\n      grid-template-columns: 4fr 1fr;\n      max-height: 3rem;\n      place-content: center;\n\n      #add-project-input, .add-project-submit-btn {\n        box-sizing: border-box;\n        appearance: none;\n        display: none;\n        border: 2px solid black;\n        padding: 5px;\n        width: 100%;\n        height: 3rem;\n      }\n\n      #add-project-input {\n        font-size: 0.9rem;\n        border-right: none;\n        border-top-left-radius: 2px;\n        border-bottom-left-radius: 2px;\n\n        &:focus {\n          outline: none;\n          background-color: #ddf;\n        }\n      }\n\n      .add-project-submit-btn {\n        background-color: #0f0;\n        border-top-right-radius: 2px;\n        border-bottom-right-radius: 2px;\n        transition: background-color 0.2s;\n\n        &:hover {\n          background-color: #2e2;\n        }\n\n        & > svg {\n          height: 100%;\n          width: 100%;\n        }\n      }\n    }\n\n    svg {\n      width: 150px;\n      height: auto;\n    }\n\n    .add-project-btn, .add-item-btn {\n      grid-column: 1 / 3;\n      background-color: transparent;\n      border: none;\n      \n      svg {\n        transition: stroke 0.2s, fill 0.2s;\n\n        &:hover {\n          stroke: #002;\n          fill: #eef;\n        }\n      }\n    }\n\n    h4 {\n      grid-column: 1 / 3;\n      text-align: center;\n    }\n  }\n}\n\n.expand-container {\n  box-sizing: border-box;\n  display: grid;\n  place-content: center;\n  place-items: center;\n\n  background-color: #ffffd0;\n  padding: 8px;\n  width: calc(100vw - var(--project-container-width) - var(--create-container-width));\n  height: calc(100vh - var(--footer-height) - var(--header-height));\n  margin: auto;\n\n  max-height: 100%;\n\n  form {\n    display: grid;\n    grid-template-columns: 5fr 3fr;\n    grid-template-rows: 1fr 9fr;\n    gap: 30px 20px;\n\n    padding: 20px;\n    border: 2px solid black;\n    background-color: white;\n\n    max-height: calc(100vh - var(--footer-height) - var(--header-height) - 100px);\n    max-width: calc(100vw - var(--project-container-width) - var(--create-container-width) - 100px);\n\n    overflow: auto;\n    scrollbar-color: lightgreen transparent;\n\n    .top {\n      grid-column: 1 / 3;\n      display: grid;\n      grid-template-columns: 1fr 9fr;\n\n      #title {\n        font-weight: 700;\n        font-size: 1.4rem;\n      }\n    }\n\n    .left {\n      display: grid;\n      grid-template: 5fr 1fr 5fr 1fr / 1fr 4fr 1fr;\n      align-items: start;\n      gap: 20px 40px;\n      \n      textarea {\n        align-self: stretch;\n        resize: none;\n      }\n\n      input, textarea {\n        grid-column: span 2;\n      }\n\n      #due-date {\n        grid-column: span 1;\n      }\n\n      .time-until {\n        align-self: center;\n        color: #333;\n        font-size: 0.8rem;\n      }\n    }\n\n    .right {\n      display: grid;\n      grid-template: 20px 300px 1fr 1fr / 1fr 1fr;\n      row-gap: 15px;\n      place-items: center;\n\n      .checklist, .add-check-div, .checklist-label {\n        grid-column: 1 / 3;\n      }\n\n      .checklist-label {\n        place-self: center start;\n      }\n\n      .checklist {\n        place-self: start;\n        display: grid;\n        align-items: center;\n        grid-template-columns: 1fr 8fr 1fr;\n        gap: 10px 10px;\n\n        padding-left: 5px;\n        max-height: 100%;\n        width: 100%;\n        overflow-y: auto;\n        scrollbar-color: purple transparent;\n        scrollbar-width: thin;\n\n        label {\n          overflow: hidden;\n          word-wrap: break-word;\n          font-size: 0.9rem;\n        }\n\n        input {\n          cursor: pointer;\n          width: 20px;\n          height: 20px;\n        }\n\n        .remove-check-btn {\n          background-color: transparent;\n          border: none;\n\n          & > svg {\n            stroke: red;\n            transition: stroke-width 0.2s;\n\n            &:hover { stroke-width: 2.5; }\n          }\n        }\n      }\n\n      .add-check-div {\n        display: grid;\n        grid-template-columns: 1fr 3fr;\n        gap: 10px;\n\n        #add-check {\n          resize: none;\n          grid-area: 1 / 2 / 3 / 3;\n          font-size: 0.7rem;\n        }\n\n        label {\n          place-self: center;\n          text-align: center;\n          font-size: 0.9rem;\n        }\n\n        .add-check-btn {\n          place-self: center;\n          font-size: 0.8rem;\n          padding: 0px 8px;\n          background-color: #90ee90;\n          &:hover { background-color: #a0eea0; }\n          &:active { background-color: #b0ffb0; }\n        }\n      }\n\n      .save {\n        background-color: #90ee90;\n        &:hover { background-color: #a0eea0; }\n        &:active { background-color: #b0ffb0; }\n      }\n\n      .exit {\n        background-color: #ff0000;\n        &:hover { background-color: #ff3535; }\n        &:active { background-color: 